// Iniciar App
document.addEventListener('DOMContentLoaded', function (){
    iniciarApp();
});

// Registro de funciones
function iniciarApp(){
    navegacionFija(); 
    scrollNav();
    menuHam();
};

// Funciones
function navegacionFija(){
    const barra = document.querySelector('.header');
    const body = document.querySelector('body');
    
    window.addEventListener('scroll', function(){
        if(body.getBoundingClientRect().top<0){
            barra.classList.add('fijar-barra');
            body.classList.add('body-scroll');
        } else{
            barra.classList.remove('fijar-barra');
            body.classList.remove('body-scroll');
        }
    });
};

function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal ul a');
    enlaces.forEach(enlace => {
        enlace.addEventListener('click', function(e){
            e.preventDefault();
            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth"});
        });
    });
};

function menuHam (){
    const ham = document.querySelector('.ham');
    const menu = document.querySelector('.menu');
    const cierres = document.querySelectorAll('.cierre')
    ham.addEventListener('click', function() {
        menu.classList.toggle('visible');
        console.log('hamburger button clicked');
    });
    cierres.forEach(cierre => {
        cierre.addEventListener('click', function(){
        menu.classList.remove('visible');
    });
});
}

// Ampliar imagenes de proyectos
const imagen = document.querySelector('.ampliar');
imagen.addEventListener('click', e => {
    if(e.target.parentElement.classList.contains('imagenes')){
        const imagen = e.target.parentElement.getAttribute('data-id');
        // console.log(imagen); 
        mostrarImagen(imagen);
    }
})

function mostrarImagen(id){
    const imagen = document.createElement('picture'); 
        imagen.innerHTML = `
            <source srcset="build/img/grande/${id}.avif" type="image/avif">
            <source srcset="build/img/grande/${id}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen proyecto">
        `;
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    overlay.onclick = function(){
        const body = document.querySelector('body');
        body.classList.remove('fijar');
        overlay.remove(); 
    };
    
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add('boton-cerrar');
    cerrarImagen.onclick = function(){
        const body = document.querySelector('body');
        body.classList.remove('fijar');
        overlay.remove();
    }
    overlay.appendChild(cerrarImagen);
    
    const body = document.querySelector('body'); 
    body.appendChild(overlay);
    body.classList.add('fijar');
}